@import url(https://fonts.googleapis.com/css?family=Courgette|Open+Sans);
body {
    position: relative;
    background-color: #01579b;
    font-family: "Open Sans", sans-serif;
}

button {
    font-style: normal;
    font-weight: bold;
    line-height: normal;
}

.center-block {
    margin: 0 auto;
    float: none;
}

/** Global Variables */
:root {
    --main-blue: #133d5e;
    --accent-blue: #01579b;
}

/** Fonts */


.login-container {
    border-radius: 0.5rem;
    margin: 10rem auto;
    float: none;
    max-width: 30em;
    padding: 2em 1em;
    overflow: hidden;
    background: rgba(229, 229, 229, 0.9);
    box-shadow: 0 0.25rem 0.25rem rgba(0, 0, 0, 0.25);
}

h1.title {
    color: var(--main-blue);
    font-family: "Courgette";
    line-height: normal;
    font-size: 3rem;
    margin: 0 auto;
    float: none;
    display: block;
    text-align: center;
    text-shadow: 0.15rem 0.15rem 0.3rem rgba(0, 0, 0, 0.15);
}

.login-btn {
    background: var(--accent-blue);
    box-shadow: 0.1rem 0.1rem 0.1rem rgba(0, 0, 0, 0.25);
    border-radius: 0.5rem;
    color: white;
    text-align: center;
    font-size: 1.25rem;
    display: block;
    margin: auto;
    margin-top: 10%;
    padding: 3% 20%;
}

.login-form input {
    display: block;
    margin: 1rem auto;
    width: 100%;
    max-width: 70%;
}

